.sectionRegisterForm {
  margin: 30px auto;
  width: 420px;
  border-radius: 20px;
  box-shadow: 0 5px 15px rgb(0 0 0 / 35%);
  padding: 50px;
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  border: 1px solid #000;
}

.headerRegisterForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100px;

}

.formRegisterForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

}

.labelRegisterForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

}

.inputRegisterForm {
  width: 200px;
  margin-bottom: 20px;
  margin-right: 20px;
  border: 1px solid rgb(116, 116, 116);
  border-radius: 10px;
  padding: 4px 10px;
}

.btnRegisterForm {
  width: 100px;
  border-radius: 10px;
  border: 1px solid rgb(116, 116, 116);
  background-color: rgb(248, 247, 247);
  box-shadow: 0 1px 5px rgb(0 0 0 / 35%);
  cursor: pointer;
  padding: 3px 7px;
}
