.sectionUserMenu {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  margin: 0;
}

.headerUserMenu{
  font-size: 20px;
  text-align: center;
}

.btnUserMenu {
  border: 1px solid rgb(233, 231, 231);
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

.btnUserMenu:hover,
.btnUserMenu:focus {
  background-color: rgb(167, 165, 165);
  color: #fff;
}

